* {
    border: none;
    text-decoration: none;
    list-style-type: none;
    padding: 0;
    margin: 0;
}

.container{
    max-width: 1280px;
    margin: auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
    box-shadow: 0px 7px 4px 0px rgba(139,132,132,0.23);
    -webkit-box-shadow: 0px 7px 4px 0px rgba(139,132,132,0.23);
    -moz-box-shadow: 0px 7px 4px 0px rgba(139,132,132,0.23);

    &__advice{
        color: #000;
        font-family: Gilroy;
        font-size: 15px;
        font-style: normal;
        font-weight: 700;
        line-height: 17px;
    }

    &__items{
        display: flex;
        justify-content: space-between;
        align-items: center;
        gap: 1rem;
    }

    &__lang__item{
        border-left: 1px solid black;
        padding: 0 0.5rem;
    }

    .header__regist__items{
        padding: 0 0.5rem;
        text-transform: capitalize;
        border-left: 2px solid #4E72CA;
    }

    &__regist{
        color: #4E72CA;
        border-bottom: 1px solid #4E72CA;
    }
    &__reg__into{
        color: #4E72CA;
    }
}

.header__link {
    color: #000000;
}

