.products {
    max-width: 1280px;
    margin: 0 auto;
    padding: 2rem 0rem;
    &__title{
        margin: 1rem 0 2rem 0;
    }
}
.text__wrap{
    display: flex;
    justify-content: space-between;
}
.wrapper__box{
    display: flex;
    gap: 1rem;
    justify-content: space-between;
}
.box__for__rendered__items{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;

}